import Vue from 'vue';
import App from './App.vue';

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

export default new Vue();

Vue.config.productionTip = true;
Vue.use(ElementUI);

new Vue({
  render: h => h(App)
}).$mount('#app');



